.nycsurf {
  text-align: center; }

.nycsurf-header {
  height: 150px;
  padding: 20px; }

.nycsurf-title {
  font-size: 4em; }

.nycsurf-main {
  margin: 0 auto;
  width: 40em; }
  .nycsurf-main h2 {
    font-size: 2em; }

.nycsurf-aside {
  position: fixed;
  top: 0;
  left: 0;
  width: 10em; }
  .nycsurf-aside--alt {
    width: auto;
    left: auto;
    right: 0; }



